import React, { ReactNode, useEffect, useState } from 'react';
import { useWixPatternsInitialProps } from '../WixPatternsInitialPropsProvider';
import { WixPatternsContainerProvider } from '../WixPatternsContainerProvider';
import { ContextType, WixPatternsEssentials } from '../../model';
import { useWixPatternsContainerOverrides } from '../WixPatternsContainerOverridesContext';
import { buildWixPatternsContainer } from '../../state';

export interface CairoProviderProps {
  value: WixPatternsEssentials;
  children?: ReactNode;
  localCache?: boolean;
  contextType?: ContextType;
}

export function CairoProvider({
  value,
  children,
  localCache = true,
  contextType = 'SITE',
}: CairoProviderProps) {
  const containerOverrides = useWixPatternsContainerOverrides();
  const initialProps = useWixPatternsInitialProps();

  const [container] = useState(() =>
    buildWixPatternsContainer(value, {
      containerOverrides,
      initialProps,
      localCache,
      contextType,
    }),
  );

  useState(() => {
    container.initialFetch();
  });

  useEffect(() => {
    return container.init();
  }, []);

  return (
    <WixPatternsContainerProvider value={container}>
      {children}
    </WixPatternsContainerProvider>
  );
}
